const selectStyles = {
  '& .MuiFormControl-root': {
    padding: '20px',
  },
};

export default function StoresLocationForm({ control }) {
  return (
    <>
      <Controller
        control={control}
        name="country"
        defaultValue={countriesList[180]}
        render={({ field: { ref, onChange, ...field } }) => (
          <Autocomplete
            options={countriesList}
            onChange={(_, data) => onChange(data)}
            defaultValue={countriesList[180]}
            className={s.autocompleteWrapper}
            renderInput={params => (
              <TextField
                {...params}
                {...field}
                fullWidth
                inputRef={ref}
                label=""
                variant="standard"
              />
            )}
          />
        )}
      />
      <Controller
        control={control}
        name="region"
        defaultValue={regionList[23]}
        render={({ field: { ref, onChange, ...field } }) => (
          <Autocomplete
            options={regionList}
            onChange={(_, data) => onChange(data)}
            defaultValue={regionList[23]}
            className={s.autocompleteWrapper}
            renderInput={params => (
              <TextField
                {...params}
                {...field}
                fullWidth
                inputRef={ref}
                label=""
                variant="standard"
              />
            )}
          />
        )}
      />
      <Controller
        control={control}
        name="citi"
        defaultValue={citiesList[8]}
        render={({ field: { ref, onChange, ...field } }) => (
          <Autocomplete
            options={citiesList}
            onChange={(_, data) => onChange(data)}
            defaultValue={citiesList[8]}
            className={`${s.autocompleteWrapper} ${s.autocompletelastChild}`}
            freeSolo
            renderInput={params => (
              <TextField
                {...params}
                {...field}
                fullWidth
                inputRef={ref}
                label=""
                variant="standard"
              />
            )}
          />
        )}
      />
      <Controller
        control={control}
        name="street"
        defaultValue=""
        render={({ field }) => (
          <TextField
            {...field}
            className={s.customInput}
            label=""
            type="search"
            variant="standard"
          />
        )}
      />

      <div className={s.houseApartmentWrapper}>
        <Controller
          control={control}
          name="houseNumber"
          defaultValue={0}
          render={({ field }) => (
            <TextField
              {...field}
              className={s.customInput}
              label=""
              type="number"
              InputLabelProps={{ className: s.customLabel }}
              variant="standard"
            />
          )}
        />
        <Controller
          control={control}
          name="apartment"
          defaultValue={0}
          render={({ field }) => (
            <TextField
              {...field}
              className={s.customInput}
              sx={selectStyles}
              label=""
              type="number"
              variant="standard"
            />
          )}
        />
      </div>
    </>
  );
}
